import React, { useState, useEffect, useMemo } from "react";
import { Table } from "react-bootstrap";
import Paginacion from "./Paginacion";

interface Props {
    itens: any[]
    mostrar: number
    row: (d: any) => any
    col: number
    children: any
}
export default function Body({ children, col, row, mostrar, itens }: Props) {
    const [page, setPage] = useState(1);
    useEffect(() => setPage(1), [itens.length]);
    function filter({ codigo, id }: any, i: number) {
        const init = (page - 1) * mostrar, fin = init + mostrar;
        return (i >= init && i < fin)
    }
    function getRef({ codigo, id }: any) {
        if (codigo) return 'key' + codigo;
        if (id) return 'key' + id;
        return 'key' + Math.random();
    }
    function Row(d: any) {
        return useMemo(() => <>{row(d)}</>, d);
    }
    function BodyContent() {
        const init = (page - 1) * mostrar, fin = init + mostrar;
        const iten = itens.filter(filter)
        return useMemo(() => <>{iten.map((it) => <Row key={getRef(it)} {...it} />
        )}</>, [...iten, init, fin]);
    }
    return <Table striped hover >
        <thead>
            {children}
        </thead>
        <tbody key={'mostrar' + page}>
            <BodyContent />
        </tbody>
        <tr>
            <td colSpan={col}>
                <Paginacion page={page} length={itens.length} mostrar={mostrar} setpage={setPage} />
            </td>
        </tr>
    </Table>
}